<template>
  <div class="page">
    <div class="page__hd">
      <div class="page__title">留言</div>
    </div>


    <div class="page__bd">
      
      <div class="weui-cells weui-cells_after-title">

        <div class="weui-cell weui-cell_input">
          <div class="weui-cell__bd">
            <textarea class="weui-textarea" placeholder="写下你的留言" @change="contentInput"></textarea> 
          </div>
        </div>
      </div>

      <div class="weui-btn-area">
        <button class="weui-btn" type="primary" @click="addComment">留言</button>
      </div>
    </div>
  </div>
</template>

<script>
import vfooter from '@/pages/footer'

export default {
  components: {
    vfooter
  },
  data () {
    return {
      uuid: '',
      comment: {
        Nickname: '',
        Content: ''
      }
    }
  },
  onLoad: function (option) {
    // console.log('comment=====:', option.uuid)
    this.uuid = option.uuid
  },
  methods: {
    contentInput: function (e) {
      this.comment.Content = e.mp.detail.value
    },
    bindFormSubmit: function (e) {
      console.log(e.detail.value.textarea)
    },
    addComment () {
      let vm = this
      console.log('comments====', vm.comment.Content)
      wx.request({
        url: 'https://www.srehub.com/lastest/wechat/small/comment',
        method: 'POST',
        data: {
          UUID: vm.uuid,
          AvatarUrl: 'https://wx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTK476rqoy0zicVAxv1fJ9v9EaltE42WibKS7btmicQBkbwBFkeB6XVSFHF4CGmFlB5OaPeVft7WJcn9w/0',
          Nickname: '阿正_Dean',
          Content: vm.comment.Content
        },
        header: {
          'content-type': 'application/json'
        },
        success: function (res) {
          console.log(res.data)
          vm.items = res.data
        }
      })
      wx.showToast({
        title: '发布成功',
        icon: 'success',
        duration: 2000,
        mask: true,
        complete: function () {
          wx.navigateBack({
            url: '/pages/airticle/main'
          })
        }
      })
    }
  }
}
</script>



<style scoped>
.weui-textarea {
  padding: 30rpx 0;
}
</style>


